@import 'https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300';

header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: 0.6s;
    padding: 40px 100px;
    z-index: 100000;
}

header.sticky {
    padding: 5px 100px;
    background: #FFFEEF;
}

header .logo {
    position: relative;
    font-weight: 700; 
    color: #FFFEEF;
    text-decoration: none;
    font-size: 2em;
    text-transform: uppercase;
    letter-spacing: 2px; 
    transition: 0.6s;
}

header ul {
    position: relative;
    display: flex;
    justify-content: center; 
    align-items: center;
    padding-right: 190px;
}

header ul li {
    position: relative; 
    list-style: none;
    
}

header ul li a {
    position: relative;
    margin: 0 20px;
    text-decoration: none;
    color: #FFFEEF;
    letter-spacing: 2px;
    font-weight: 500;
    transition: 0.6s;
}

header.sticky ul li a {
    color: #000;
}